  --backgroundColor: #475569;
  --primaryColor: #334155;
  --secondaryColor: #4682b4;
  --textBackground: #94a3b8;
  --hoverColor: #4682b4;
  --hoverTextColor: #334155;
  --textBoxColor: white;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Roboto", sans-serif;
}

html,
body {
  height: 100%;
  margin: 0;
}

body {
  display: flex;
  flex-direction: column;
  background-color: var(--backgroundColor);
}

main {
  display: flex;
  flex: 1;
  flex-direction: column;
  align-items: center;
  background-color: var(--backgroundColor);
  overflow-x: hidden;
}

.main-container {
  display: flex;
  flex: 1;
  justify-content: center;
  margin: 16px;
}

.main-form-container {
  margin: 12px;
  flex: 1;
  display: flex;
  flex-direction: column;
}

input {
  background-color: var(--textBoxColor);
  border-style: none;
}

input[type=text],
option {
  height: 20px;
  padding: 5px;
}

section {
  border-bottom: solid black 1px;
  margin-bottom: 6px;
  padding-bottom: 10px;
}

/* Header */
header {
  height: 40px;
  display: flex;
  width: 100%;
  background-color: var(--primaryColor);
}

.nav-form,
.nav-saved {
  flex: 1; /* Distribute the space evenly */
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  cursor: pointer;
}

/* Specific styles for each tab */
.nav-form {
  background-color: var(--backgroundColor);
  border-radius: 8px 8px 0px 0px;
}

.nav-saved {
  background-color: var(--primaryColor);
}

/* Optional: Add hover effects for better user experience */
.nav-form:hover,
.nav-saved:hover {
  background-color: var(--hoverColor); /* Define a hover color in your variables */
  color: var(--hoverTextColor); /* Optional: Change text color on hover */
}

/* Section 1 */
.section-1 {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 10px; /* Adjust the gap between labels and inputs as needed */
  align-items: center; /* Center align labels and inputs vertically */
  padding: 8px;
}

.section-1 label {
  justify-self: end; /* Align labels to the right */
}

/* Section 2 */
.section-2 {
  display: grid;
  grid-template-columns: 1fr;
  gap: 10px; /* Adjust the gap as needed */
  padding: 8px;
}

.checkbox-container-title {
  font-weight: bold;
}

.checkboxes {
  display: grid;
  grid-template-columns: repeat(4, auto 1fr);
  gap: 10px; /* Adjust the gap between labels and checkboxes as needed */
  align-items: center; /* Center align labels and checkboxes vertically */
}

.checkbox {
  display: contents; /* Use contents to make the children directly in the grid */
}

.checkbox label {
  justify-self: end; /* Align labels to the right */
}

.checkbox input[type=checkbox] {
  width: 20px; /* Set the desired width of checkbox */
  height: 20px; /* Set the desired height of checkbox */
  margin: 0; /* Remove default margin */
  cursor: pointer;
}

/* Section 3 */
.section-3 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px; /* Adjust the gap between elements as needed */
  align-items: center;
  max-width: 600px; /* Adjust the width as needed */
  margin: auto; /* Center the section */
}

.text-box {
  display: flex;
  flex-direction: column;
}

.text-box label {
  margin-bottom: 5px;
}

.text-box input {
  width: 100%;
  box-sizing: border-box;
}

/* Notes Container */
.notes-container {
  padding: 8px;
  max-height: 350px;
  flex: 1;
  display: flex;
}

.notes {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--textBackground);
  border-radius: 8px;
  flex: 1;
}

textarea {
  width: 100%;
  height: 100%;
  margin: 8px;
  color: black;
  border-style: none;
  background-color: transparent;
  padding: 6px;
  flex: 1;
}

textarea:focus {
  border-style: none;
  outline: none;
}

/* Second Container */
.second-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: solid black 1px;
  border-radius: 8px;
  max-height: 750px;
  width: 450px;
  overflow-y: auto;
}

.add-transaction {
  padding: 6px;
  text-align: center;
}

.add-transaction-button {
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
  border-style: none;
  background-color: transparent;
  font-size: 1.1rem;
  font-weight: 250;
}

.add-transaction-button:hover {
  transform: scale(1.2);
}

.buttons-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 12px;
  margin: 12px;
}

.buttons-container button {
  padding: 8px;
  background-color: var(--secondaryColor);
  border-style: none;
  border-radius: 8px;
  cursor: pointer;
}

/* Transaction Forms */
#transaction-container {
  display: flex;
  flex-direction: column;
}

.transaction-card {
  background-color: var(--secondaryColor);
  padding: 10px;
  margin: 8px;
  border-radius: 8px;
  position: relative;
  display: flex;
  flex-direction: column;
}

.transaction-card:focus,
.transaction-card:active {
  border: solid black 1px;
}

.secondary-card-container {
  display: flex;
  flex-direction: column;
}

.transaction-checkbox {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 6px;
}

.transaction-checkbox label {
  justify-self: start;
}

.transaction-checkbox input {
  justify-self: start;
  width: 20px;
  height: 20px;
  margin: 0;
  cursor: pointer;
  margin-left: 6px;
}

.transaction-title {
  background-color: transparent;
  font-size: 1.2rem;
  font-weight: bold;
  text-align: center;
}

.close-card {
  position: absolute;
  top: 0;
  right: 0;
  background: none;
  border: none;
  font-size: 1rem;
  cursor: pointer;
  margin: 6px;
  transition: transform 1s ease-in;
}

.close-card:hover {
  transform: scale(1.5);
}

.transaction-card h3 {
  border-bottom: solid black 1px;
  padding: 4px;
  margin-bottom: 6px;
}

.option-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
  padding: 6px;
}

.option-container label {
  justify-self: start;
}

.text-input-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  gap: 10px;
  padding: 6px;
}

.text-input-container label {
  justify-self: start;
  padding-right: 6px;
}

.radio-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 6px;
}

.radios {
  display: flex;
  justify-content: start;
  gap: 12px;
}

.tax-witholding-container {
  border: solid black 1px;
  padding: 6px;
  border-radius: 6px;
}

.text-input-container.three {
  grid-template-columns: 1fr 0.9fr 0.1fr;
}

/* Footer  */
footer {
  position: sticky;
  width: 100%;
  padding: 10px;
  background-color: var(--primaryColor);
  box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 0;
}

.persistent-note {
  width: 60%;
}

textarea#persistent-notes {
  width: 100%;
}

.important-buttons {
  display: flex;
  flex-direction: column;
  margin: 8px;
}

#save-persistent-button,
#reset-persistent-button {
  padding: 8px;
  background-color: var(--textBackground);
  cursor: pointer;
  margin: 6px;
  border-style: none;
  border-radius: 6px;
}

/* Submit Button */
.submit-reset-buttons {
  justify-self: end;
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 0.5rem;
  gap: 16px;
}

#submit-button,
#reset-button {
  background-color: var(--secondaryColor);
  cursor: pointer;
  font-size: 1rem;
  border-style: none;
  border-radius: 8px;
  padding: 8px;
}

#submit-button:hover,
#reset-button:hover {
  background-color: var(--primaryColor);
  color: var(--hoverColor);
}

/* Overlay */
#overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--primaryColor);
  z-index: 2;
  display: none;
}

#overlay .header {
  display: flex;
  width: 100%;
}

#overlay-notes-container {
  width: 100%;
  height: 100%;
  padding: 24px;
  font-size: 1.3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#overlay-notes-container ul {
  padding-left: 24px;
  margin: 0;
}

#overlay-notes-container ul li {
  margin-bottom: 16px; /* Space between list items */
  list-style-type: disc;
}

.overlay-note {
  display: flex;
  flex-direction: column;
}

.note-title {
  display: flex;
  gap: 12px;
  align-items: center;
}

.expand-modal-btn {
  padding: 5px;
  font-size: 1rem;
  background-color: var(--textBackground);
  border-radius: 8px;
  border-style: none;
  cursor: pointer;
}

.note-modal {
  width: 80%;
  height: 80%;
  position: fixed;
  top: 50%;
  left: 50%;
  transform-origin: center;
  transform: translate(-50%, -50%);
  margin: 12px;
  padding: 12px;
  border-radius: 8px;
  display: none;
  background-color: var(--textBackground);
  opacity: 0;
}

.close-modal-btn {
  width: 24px;
  height: 24px;
  position: fixed;
  top: 0;
  right: 0;
  margin: 8px;
  font-size: 1.5rem;
  background-color: transparent;
  border-style: none;
  cursor: pointer;
}

.note-modal.show {
  opacity: 100%;
  pointer-events: all;
  display: flex;
  flex-direction: column;
}

.note-print {
  padding: 12px;
  width: 100%;
}

.backdrop {
  pointer-events: none;
  position: fixed;
  inset: 0;
  background: #000;
  opacity: 0%;
}

.backdrop.show {
  opacity: 30%;
}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAEA;EACE,0BAAA;EACA,uBAAA;EACA,yBAAA;EACA,yBAAA;EACA,qBAAA;EACA,yBAAA;EACA,qBAAA;AAAF;;AAGA;EACE,sBAAA;EACA,UAAA;EACA,SAAA;EACA,iCAAA;AAAF;;AAGA;;EAEE,YAAA;EACA,SAAA;AAAF;;AAGA;EACE,aAAA;EACA,sBAAA;EACA,wCAAA;AAAF;;AAGA;EACE,aAAA;EACA,OAAA;EACA,sBAAA;EACA,mBAAA;EACA,wCAAA;EACA,kBAAA;AAAF;;AAGA;EACE,aAAA;EACA,OAAA;EACA,uBAAA;EACA,YAAA;AAAF;;AAGA;EACE,YAAA;EACA,OAAA;EACA,aAAA;EACA,sBAAA;AAAF;;AAGA;EACE,qCAAA;EACA,kBAAA;AAAF;;AAGA;;EAEE,YAAA;EACA,YAAA;AAAF;;AAGA;EACE,8BAAA;EACA,kBAAA;EACA,oBAAA;AAAF;;AAGA,WAAA;AACA;EACE,YAAA;EACA,aAAA;EACA,WAAA;EACA,qCAAA;AAAF;;AAGA;;EAEE,OAAA,EAAA,gCAAA;EACA,aAAA;EACA,mBAAA;EACA,uBAAA;EACA,aAAA;EACA,eAAA;AAAF;;AAGA,iCAAA;AACA;EACE,wCAAA;EACA,8BAAA;AAAF;;AAGA;EACE,qCAAA;AAAF;;AAGA,2DAAA;AACA;;EAEE,mCAAA,EAEG,2CAAA;EACH,4BAAA,EAAA,yCAAA;AAFF;;AAKA,cAAA;AACA;EACE,aAAA;EACA,+BAAA;EACA,SAAA,EAAA,uDAAA;EACA,mBAAA,EAAA,8CAAA;EACA,YAAA;AAFF;;AAKA;EACE,iBAAA,EAAA,8BAAA;AAFF;;AAKA,cAAA;AAEA;EACE,aAAA;EACA,0BAAA;EACA,SAAA,EAAA,6BAAA;EACA,YAAA;AAHF;;AAMA;EACE,iBAAA;AAHF;;AAMA;EACE,aAAA;EACA,0CAAA;EACA,SAAA,EAAA,2DAAA;EACA,mBAAA,EAAA,kDAAA;AAHF;;AAMA;EACE,iBAAA,EAAA,2DAAA;AAHF;;AAMA;EACE,iBAAA,EAAA,8BAAA;AAHF;;AAMA;EACE,WAAA,EAAA,sCAAA;EACA,YAAA,EAAA,uCAAA;EACA,SAAA,EAAA,0BAAA;EACA,eAAA;AAHF;;AAMA,cAAA;AAEA;EACE,aAAA;EACA,qCAAA;EACA,SAAA,EAAA,8CAAA;EACA,mBAAA;EACA,gBAAA,EAAA,+BAAA;EACA,YAAA,EAAA,uBAAA;AAJF;;AAOA;EACE,aAAA;EACA,sBAAA;AAJF;;AAOA;EACE,kBAAA;AAJF;;AAOA;EACE,WAAA;EACA,sBAAA;AAJF;;AAOA,oBAAA;AAEA;EACE,YAAA;EACA,iBAAA;EACA,OAAA;EACA,aAAA;AALF;;AAQA;EACE,aAAA;EACA,sBAAA;EACA,mBAAA;EACA,uCAAA;EACA,kBAAA;EACA,OAAA;AALF;;AAQA;EACE,WAAA;EACA,YAAA;EACA,WAAA;EACA,YAAA;EACA,kBAAA;EACA,6BAAA;EACA,YAAA;EACA,OAAA;AALF;;AAQA;EACE,kBAAA;EACA,aAAA;AALF;;AAQA,qBAAA;AAEA;EACE,aAAA;EACA,sBAAA;EACA,mBAAA;EACA,uBAAA;EACA,kBAAA;EACA,iBAAA;EACA,YAAA;EACA,gBAAA;AANF;;AASA;EACE,YAAA;EACA,kBAAA;AANF;;AASA;EACE,eAAA;EACA,sCAAA;EACA,kBAAA;EACA,6BAAA;EACA,iBAAA;EACA,gBAAA;AA